<template>
  <div class="page-swap" :key="$route.name">
    <div class="from-wrap">
      <div class="from form-control">
        <div class="label">From <span @click="setMax">MAX</span></div>
        <div class="input" :class="{nounit: !from.token}">
          <input type="text" placeholder="0.000" v-model="from.val" @change="handlerInput('from', $event)"/>
          <span class="unit" @click="selectToken('from')">{{from.token || '选择'}} <i>▲</i></span>
        </div>
      </div>
      <div class="arrow" ><img @click="toggleFromTo" src="../../assets/exchange-arrow.png" /></div>
      <div class="to form-control">
        <div class="label">To (estimated)</div>
        <div class="input" :class="{nounit: !to.token}">
          <input type="text" placeholder="0.000" v-model="to.val" @input="handlerInput('to', $event)" />
          <span class="unit" @click="selectToken('to')">{{to.token || '选择'}} <i>▲</i></span>
        </div>
      </div>
    </div>
    <div class="act">
      <Button v-if="!$s.account.isLogin" type="flat" @click="$modal('wallet')">Connect Wallet</Button>
      <Button type="flat" disabled v-else-if="!from.token || !to.token">选择代币</Button>
      <Button type="flat" disabled v-else-if="!from.val && !to.val">输入一个数量</Button>
      <Button type="flat" :disabled="from.approveState == 2" v-else-if="from.approveState == 1 || from.approveState == 2" @click="approveExchange">{{approveText(from.approveState)}} {{from.token}}</Button>
      <Button type="flat" :disabled="to.approveState == 2" v-else-if="to.approveState == 1 || to.approveState == 2" @click="approveExchange">{{approveText(to.approveState)}} {{to.token}}</Button>
      
    </div>
  </div>
</template>
<script>
import BigNumber from 'bignumber.js'
const ApproveUnknow = 0
const ApproveNot = 1
const ApprovePendding = 2
const Approveed = 3

const tokenDefault = {
  token: '',
  tokenAddress: '',
  approveState: ApproveUnknow,
  balance: new BigNumber(0),
  allowance: new BigNumber(0),
}

export default {
  name: "exchange-swap",
  data() {
    return {
      from: {
        val: '',
        ...tokenDefault,
        token: 'ETH',
      },
      to: {
        val: '',
        ...tokenDefault,
      },
      inputField: ''
    };
  },
  $computed: {
    isLogin: 'account.isLogin',
  },
  watch: {
    '$s.account.address': function(v) {
      if (v) {
        this.updateToken('from')
        this.updateToken('to')
      }
    }
  },
  mounted() {
    
  },
  methods: {
    approveText(state) {
      switch(state) {
        case ApproveUnknow:
        case ApproveNot:
          return 'Approve'
        case ApprovePendding:
          return 'Approving...'
        case Approveed:
          return 'Approved'
      }
    },
    // 输入数值
    handlerInput(inputField, e) {
      this.inputField = inputField
      this[inputField].val = (e.target.value || '').replace(/[^0-9\.]/ig, '')
      this.$forceUpdate()
      // TODO 根据汇率计算
      // 。。。
    },
    setMax() {
      this.from.val = !this.from.balance ? '0' : String(this.from.balance.toNumber())
    },
    // from to 互换
    toggleFromTo() {
      const from = this.from
      this.from = this.to
      this.to = from
    },
    // 选择代币
    selectToken(target) {
      const token = this[target].token
      this.$modal('selectToken', {
        select: token,
        accept: item => {
          console.log(item, this, this[target])
          const data = this[target]
          this[target] = {
            val: data.val,
            ...tokenDefault,
            token: item.symbol,
            tokenAddress: item.tokenAddress
          }
          this.updateToken(target)
        }
      })
    },
    // 更新代币状态
    async updateToken(target) {
      if (!this.isLogin) {
        return
      }
      const data = this[target]
      if (!data.token) {
        return
      }
      data.approveState = ApprovePendding
      const ta = data.tokenAddress
      const tokeninfo = await this.$s.exchange.swapTokenInfo(data.token)
      data.balance = tokeninfo.balance
      data.allowance = tokeninfo.allowance
      data.approveState = !tokeninfo.allowance.toNumber() ? ApproveNot : Approveed
    },
    // 代币授权
    async approveExchange() {
      if (this.approveState !== ApproveNot) {
        console.error('approve was called unnecessarily')
        return
      }
      this.$s.exchange.approve(this.from, this.to)
    },
    onSwap() {

    }
  },
};
</script>
<style lang="less" scoped>
.page-swap {
  .from-wrap {
    margin-top: 15px;
    & > div {
      display: inline-block;
    }
    .arrow {
      width: 10%;
      // margin-left: 25px;
      // margin-right: 25px;
      vertical-align: top;
      // margin-top: 60px;
      text-align: center;
      height: 137px;
      line-height: 137px;
      img {
        width: 20px;
        width: 20px;
        vertical-align: middle;
        cursor: pointer;
      }
    }
    .form-control {
      width: 45%;
      padding: 24px;
      box-sizing: border-box;
      font-size: 22px;
      background: #fafafa;
      border-radius: 15px;
      .label {
        margin-bottom: 19px;
        span {
          float: right;
          color: #3b76fc;
          cursor: pointer;
        }
      }
      .input {
        white-space: nowrap;
        display: flex;
        input {
          border: none;
          outline: none;
          background: none;
          font-size: 38px;
          display: inline-block;
          color: #000;
          width:100%;
          padding: 0;
          &:-webkit-input-placeholder {
            color: #ccc;
          }
        }
        .unit {
          display: inline-block;
          width: auto;
          height: 45px;
          line-height: 45px;
          vertical-align: top;
          white-space: nowrap;
          text-align: right;
          cursor: pointer;
          text-transform: uppercase;
          border-radius: 9px;
          padding: 0 10px;
          box-sizing: border-box;
          &:hover {
            background: #ffd0254d;
          }
          i {
            transform: rotate(180deg);
            display: inline-block;
            font-size: 14px;
            height: 45px;
            line-height: 45px;
            vertical-align: bottom;
          }
        }
        &.nounit {
          .unit {
            background: #ffd0254d;
            color: #555;
          }
        }
      }
    }
    @media screen and (max-width: 760px) {
      .form-control {
        width: 100%;
      }
      .arrow {
        margin: 30px 0;
        width: 100%;
        height: 30px;
        line-height: 30px;
        img {
          transform: rotate(90deg);
        }
      }
    }
  }
  .act {
    text-align: center;
    margin-top: 85px;
    margin-bottom: 25px;
    .act-btn {
      padding: 0 30px;
      // width: 256px;
    }
  }
}
</style>